<template>
    <div class="collect-configuration">
        <div class="personal-center-title"><h3></h3></div>
        <div class="collect-configuration-area" v-for="key in data">
            <ul>
                <li>
                    <div></div>
                    <div>eTerm</div>
                    <div>eTerm</div>
                    <div>{{key.info.etermPort}}</div>
                </li>
                <li>
                    <div></div>
                    <div>{{key.info.user}}</div>
                    <div>si</div>
                    <div>*********</div>
                </li>
                <li>
                    <div></div>
                    <div>*********</div>
                    <div>h</div>
                    <div>{{key.info.acquisitionT}}</div>
                </li>
                <li>
                    <div></div>
                    <div>{{key.info.jobn}}</div>
                    <div>m</div>
                    <div>{{key.info.conflict}}</div>
                </li>
                <li>
                    <div></div>
                    <div>*********</div>
                    <div></div>
                    <div>{{key.info.lastTime}}</div>
                </li>
                <li>
                    <div></div>
                    <div>{{key.info.level}}</div>
                    <div></div>
                    <div v-if="key.info.state==0"></div>
                    <div v-else-if="key.info.state==1"></div>
                    <div v-else-if="key.info.state==2"></div>
                </li>
                <li>
                    <div>eTermIP</div>
                    <div>{{key.info.etermIp}}</div>
                    <div class="click-btn">
                        <span class="click-btn-test" @click="testConnect(key.info.id)"></span>
                    </div>
                </li>
            </ul>
            <div>
                <div class="collect-change"  @click="amend(key)"></div>
                <div class="collect-clear" @click="deletes(key.info.id)">
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                data:''
            }
        },
        props:['up_date'],
        watch:{
            up_date:function () {
                this.req();
            }
        },
        mounted:function () {
            this.req();
        },
        methods:{
            req:function () {
                $.ajax({
                    type:'post',
                    url:'/getEtermAirportOnLineData',
                    dataType:'json',
                    success:(data)=>{
                        this.data=data;
                    }
                })
            },
            testConnect:function (val) {
                this.$emit('test_etermId',val);
            },
            deletes:function (val) {
               if(confirm("")){
                   $.ajax({
                       type:'get',
                       data:{
                           id:val
                       },
                       dataType:'json',
                       url:"/eterm_delete",
                       success:(data)=>{
                           if(data.success){
                               this.req();
                           }else{
                               alert("");
                           }
                       }
                   });
               }
            },
            amend:function (val) {
                this.$emit('amend',val);
            }
        }
    }
</script>
<style>

</style>


